<template>
    <div class="my-notes-page">

      <h1>My Notes</h1>
      <h2>This is the place where you can view all notes, highlighted sections, and bookmarked content</h2>

      
      <button @click="viewNotes">View Notes</button>
      <div v-if="showNotes">
        <div v-for="(note, index) in notes" :key="index">
          {{ note }}
        </div>
      </div>
    </div>
  </template>
  
  <script>
  export default {
    props: ['notes'],
    data() {
      return {
        showNotes: false,
      };
    },
    methods: {
      viewNotes() {
        this.showNotes = true;
      },
    },
  };
  </script>
  